import React, { useState, useEffect, useRef } from 'react'
import {
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CRow,
  CForm,
  CButton,
  CFormLabel,
  CFormInput,
  CFormFeedback,
  CButtonGroup,
  CFormSwitch,
  CFormSelect,
  CModal,
  CModalHeader,
  CModalBody,
  CModalFooter,
  CModalTitle,
} from '@coreui/react'

import Select from 'react-select'

import Cropper from 'react-cropper'
import 'cropperjs/dist/cropper.css'

import { Authentification } from '../../components/index'

import { useForm, Controller } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import * as yup from 'yup'

import { Link, useHistory, useParams } from 'react-router-dom'

import Message from '../../components/Message'
import Loader from '../../components/Loader'

import { getFoods, addFood } from '../../provider/foodFetch'
import { getOptionLists, addOptionList } from '../../provider/optionListFetch'
import { getCategories, addCategorie } from '../../provider/categorieFetch'

const schemaUser = yup.object().shape({
  name: yup.string().required('name is required'),
  description: yup.string().required('description is required'),
  price: yup
    .number()
    .typeError('price must be a `number` type')
    .required('price are required'),
  // priceDrop: yup.number(),
  // priceDrop: yup.nullable(),
  category: yup.string().required('categorie are required'),
  optionLists: yup.array(),
  isPopular: yup.boolean(),
  available: yup.boolean(),
  promotion: yup.boolean(),
  // countInStock: yup.number().required("priceDrop are required"),
})

const AjoutFood = () => {
  /** State */
  const [message, setMessage] = useState(null)
  const [success, setSuccess] = useState(false)
  const [error, setError] = useState(false)
  const [categories, setCategories] = useState(null)
  const [optionLists, setOptionLists] = useState(null)
  const [visible, setVisible] = useState(false)

  /** Image */
  const cropperRef = useRef(null)
  const [croppedImg, setCroppedImg] = useState('')
  const [image, setImage] = useState(null)

  const onCrop = () => {
    const imageElement = cropperRef?.current
    const cropper = imageElement?.cropper
    setCroppedImg(cropper.getCroppedCanvas().toDataURL())
  }

  const handleFileSelect = async (e) => {
    try {
      e.preventDefault()
      let files

      if (e.dataTransfer) {
        files = e.dataTransfer.files
      } else if (e.target) {
        files = e.target.files
      }

      const reader = new FileReader()

      if (!reader) return

      reader.onload = () => {
        setImage(reader.result?.toString())
        e.target.value = null
        setVisible(true)
      }

      reader.readAsDataURL(files[0])
    } catch (error) {
      console.log(error)
    }
  }

  /** Hook Route */
  let history = useHistory()
  let { id: foodId } = useParams()
  const paramQuery = window.location.hash
  const paramsJS = new URLSearchParams(
    paramQuery.substring(paramQuery.lastIndexOf('?'))
  )
  const restaurantParam = paramsJS.has('restaurant')
    ? paramsJS.get('restaurant')
    : 'restaurant'
  const categoryParam = paramsJS.has('category')
    ? paramsJS.get('category')
    : 'category'
  const optionListsParam = paramsJS.has('optionLists')
    ? paramsJS.get('optionLists')
    : []

  console.log(
    'params',
    paramQuery.substring(paramQuery.lastIndexOf('?')),
    categoryParam,
    optionListsParam,
    restaurantParam
  )

  /** Form Hook */
  const {
    register,
    handleSubmit,
    formState: { errors, isSubmitting, isValid },
    resetField,
    setValue,
    // getValues,
    control,
  } = useForm({
    resolver: yupResolver(schemaUser),
    mode: 'onTouched',
    defaultValues: { priceDrop: 0 },
  })

  /** Run once, when the component mounts */
  useEffect(() => {
    console.log('Run once, when the component mounts ')

    async function fetch() {
      const resp = await getOptionLists(null, null)

      const resp1 = await getCategories(null, null)

      if (resp?.status) {
        setOptionLists([
          ...resp.data.map((o) => {
            return { value: o._id, label: o.name }
          }),
        ])
      } else {
        setMessage(resp?.error)
      }

      if (resp1?.status) {
        setCategories([...resp1.data])
      } else {
        setMessage(resp1?.error)
      }
    }

    fetch()
  }, [])

  /** Dependency */
  useEffect(() => {
    console.log('useEffect, Dependency')

    if (foodId) {
      async function fetch() {
        const resp = await getFoods(foodId)

        console.log('resp', resp)

        if (resp?.status) {
          setValue('name', resp?.data.name)
          setValue('description', resp?.data.description)
          setValue('price', resp?.data.price)
          setValue('priceDrop', resp?.data.priceDrop)
          setValue('category', resp?.data.category._id)
          setValue('isPopular', resp?.data.isPopular)
          setValue('available', resp?.data.available)
          setValue('promotion', resp?.data.promotion)
          // setValue("countInStock", resp?.data.countInStock);

          if (resp?.data.optionLists) {
            if (resp?.data.optionLists.length) {
              /** optionLists */
              const optionListTMP = resp?.data.optionLists.map((o) => {
                return { value: o._id, label: o.name }
              })
              setValue('optionLists', optionListTMP)
            }
          }
        } else {
          setMessage(resp?.error)
        }
      }

      fetch()
    }
  }, [foodId, setValue])

  const onSubmit = async (data) => {
    let resp = null
    let body = null

    if (data?.optionLists) {
      if (data?.optionLists?.length)
        body = { ...data, optionLists: data.optionLists.map((o) => o.value) }
    } else {
      console.log('body', body)
      body = { ...data }
    }

    console.log('onSubmit', data, body)
    // console.log("isValid", isValid);

    if (isValid && body) {
      /** reste */
      setSuccess(false)
      setError(false)
      resp = await addFood(body, foodId)
    }

    if (resp?.status) {
      if (!foodId) {
        /** Reset */
        resetField('name')
        resetField('description')
        resetField('price')
        resetField('priceDrop')
        resetField('category')
        resetField('optionLists')
        resetField('isPopular')
        resetField('available')
        resetField('promotion')
        // resetField("countInStock");
      }

      if (body.category) {
        if (restaurantParam)
          await addCategorie({ restaurant: restaurantParam }, body.category)
      }

      if (body.optionLists) {
        if (body.optionLists.length) {
          for (const optionListFor of body.optionLists) {
            await addOptionList({ foods: [resp?.data?._id] }, optionListFor)
          }
        }
      }

      setSuccess(true)
    } else {
      setError(true)
      setMessage(resp?.error)
    }
  }

  const back = () => {
    history.push(`/restaurants`)
  }

  if (categories === null || optionLists === null) {
    return <Loader />
  }

  console.log('errors', errors)

  return (
    <>
      <CRow>
        <Authentification />
        {error && <Message> {message}</Message>}
        {success && <Message> {`Success Ajout ou Edite`}</Message>}
        <CCol xs={12}>
          <CCard className="mb-4">
            <CCardHeader>
              <CRow>
                <CCol sm={5}>
                  <strong>Ajout Food</strong>
                </CCol>
                <CCol sm={7}>
                  <CButtonGroup className="float-end me-3">
                    <CButton
                      color="outline-secondary"
                      className="mx-0"
                      onClick={() => back()}
                    >
                      Retour
                    </CButton>
                    <label
                      className="btn btn-outline-primary mx-1"
                      htmlFor="imageNRH"
                    >
                      Ajout image
                    </label>
                    <input
                      id="imageNRH"
                      style={{
                        display: 'none',
                      }}
                      type="file"
                      onChange={handleFileSelect}
                    />
                  </CButtonGroup>
                </CCol>
              </CRow>
            </CCardHeader>
            <CCardBody>
              <CForm
                noValidate
                className={`row g-3`}
                onSubmit={handleSubmit(onSubmit)}
              >
                <CCol md={6}>
                  <CFormLabel htmlFor="name">Name</CFormLabel>
                  <CFormInput
                    type="text"
                    id="name"
                    required
                    {...register('name')}
                    invalid={errors.name ? true : false}
                  />
                  <CFormFeedback invalid>{errors.name?.message}</CFormFeedback>
                </CCol>
                <CCol md={6}>
                  <CFormLabel htmlFor="lastName">Description</CFormLabel>
                  <CFormInput
                    type="text"
                    id="description"
                    required
                    {...register('description')}
                    invalid={errors.description ? true : false}
                  />
                  <CFormFeedback invalid>
                    {errors.description?.message}
                  </CFormFeedback>
                </CCol>
                <CCol md={6}>
                  <CFormLabel htmlFor="price">Price</CFormLabel>
                  <CFormInput
                    type="number"
                    id="price"
                    required
                    {...register('price')}
                    invalid={errors.price ? true : false}
                  />
                  <CFormFeedback invalid>{errors.price?.message}</CFormFeedback>
                </CCol>
                <CCol md={6}>
                  <CFormLabel htmlFor="priceDrop">PriceDrop</CFormLabel>
                  <CFormInput
                    type="number"
                    id="priceDrop"
                    {...register('priceDrop')}
                  />
                  <CFormFeedback invalid>
                    {errors.priceDrop?.message}
                  </CFormFeedback>
                </CCol>
                <CCol md={3}>
                  <CFormSwitch
                    label="isPopular"
                    id="isPopular"
                    {...register('isPopular')}
                  />
                </CCol>
                <CCol md={3}>
                  <CFormSwitch
                    label="available"
                    id="available"
                    {...register('available')}
                  />
                </CCol>
                <CCol md={3}>
                  <CFormSwitch
                    label="promotion"
                    id="promotion"
                    {...register('promotion')}
                  />
                </CCol>
                <CCol md={6}>
                  <CFormLabel htmlFor="categorie">Categorie</CFormLabel>
                  <CFormSelect
                    aria-label="Categorie"
                    id="categorie"
                    required
                    {...register('category')}
                    invalid={errors.category ? true : false}
                    defaultValue={categories[0]?._id && categories[0]?._id}
                  >
                    {categories?.length &&
                      categories.map((c) => (
                        <option value={c._id} key={c._id}>
                          {c.name}
                        </option>
                      ))}
                  </CFormSelect>
                  <CFormFeedback invalid>
                    {errors.category?.message}
                  </CFormFeedback>
                  <small className="form-text text-muted">
                    <Link to={`/ajout-categorie?restaurant=${restaurantParam}`}>
                      Ajouter une categorie
                    </Link>{' '}
                  </small>
                </CCol>
                <CCol md={6}>
                  <CFormLabel htmlFor="optionLists">Option Liste</CFormLabel>
                  <Controller
                    control={control}
                    name="optionLists"
                    render={({
                      field: { onChange, onBlur, value, name },
                      // field,
                      // fieldState: { error },
                    }) => (
                      <>
                        <Select
                          defaultValue={value}
                          value={value}
                          name={name}
                          onBlur={onBlur}
                          onChange={onChange}
                          // {...field}
                          isMulti
                          options={optionLists}
                        />
                        <small className="form-text text-muted">
                          <Link
                            to={`/ajout-optionList?foods=${`foods`}&options=${`options`}`}
                          >
                            Ajouter un option Liste
                          </Link>{' '}
                        </small>
                        {errors.optionLists?.message && (
                          <div
                            style={{ display: 'block' }}
                            className="invalid-feedback"
                          >
                            {errors.optionLists?.message}
                          </div>
                        )}
                      </>
                    )}
                  />
                </CCol>
                <CCol xs={12}>
                  <CButton
                    color="primary"
                    type="submit"
                    className={isSubmitting ? 'disabled' : ' '}
                  >
                    Submit
                  </CButton>
                </CCol>
              </CForm>
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>

      <CModal size='lg' visible={visible} onClose={() => setVisible(false)}>
        <CModalHeader onClose={() => setVisible(false)}>
          <CModalTitle>Upload Image</CModalTitle>
        </CModalHeader>
        <CModalBody>
          <Cropper
            src={image}
            style={{ height: 500, width: '732px' }}
            initialAspectRatio={16 / 9}
            guides={false}
            crop={onCrop}
            ref={cropperRef}
            viewMode={1}
            // guides={true}
            minCropBoxHeight={10}
            minCropBoxWidth={10}
            // background={false}
            responsive={true}
            autoCropArea={1}
            aspectRatio={4 / 3}
            checkOrientation={false}
          />
        </CModalBody>
        <CModalFooter>
          <CButton color="secondary" onClick={() => setVisible(false)}>
            Close
          </CButton>
          <CButton color="primary">Save changes</CButton>
        </CModalFooter>
      </CModal>
    </>
  )
}
export default AjoutFood
